<template>
  <div class="stock">
    <div class="form-wrapper">
      <el-form :model="ruleForm" :rules="rules" :size="size" ref="ruleForm" class="rule-form" label-width="100px">
        <label class="title">数量信息</label>
        <el-form-item label="样板编号" prop="templateId">
          <el-input v-model="ruleForm.templateId"></el-input>
        </el-form-item>
        <el-form-item label="数量（片）" prop="templateAmount">
          <el-input v-model="ruleForm.templateAmount"></el-input>
        </el-form-item>
        <hr>
        <label class="title">存放地点信息</label>
        <el-form-item label="产区" prop="templateAreas">
          <el-input v-model="ruleForm.templateAreas"></el-input>
        </el-form-item>
        <el-form-item label="分厂" prop="templateFactory" v-if="!(auth === 'factorier')">
          <el-input v-model="ruleForm.templateFactory"></el-input>
        </el-form-item>
        <el-form-item label="存放点（货架编号）" prop="templateStore">
          <el-input v-model="ruleForm.templateStore"></el-input>
        </el-form-item>
        <hr>
        <label class="title">图片信息</label>
        <el-upload
          ref="uploadPic"
          :action="uploadUrl"
          :limit="5"
          :auto-upload="false"
          :before-upload="newFiles"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-exceed="uploadOverrun"
          list-type="picture-card"
          class="upload" multiple>
          <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
          <div class="tip" slot="tip">只能上传最多5张照片，且不超过10M</div>
        </el-upload>
        <hr>
        <label class="title">其他信息</label>
        <el-form-item label="备注" prop="templateMemo">
          <el-input v-model="ruleForm.templateMemo"></el-input>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
      <el-button @click="resetForm('ruleForm')">重置</el-button>
    </div>
  </div>
</template>

<script>
  import * as api from 'api/index'
  import { mapGetters } from 'vuex'
  
  export default {
    name: "stock",
    data() {
      var numberValidate = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('This field is required'))
        }
        else if (isNaN(value)) {
          callback(new Error('Please enter a number'))
        }
        else {
          callback()
        }
      }
      
      var idValidate = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('This field is required'))
        }
        api.IsIdExist.r(value).then(({ data }) => {
          if (!data) {
            callback(new Error('该样板不存在'))
          }
          else {
            callback()
          }
        }).catch((err) => {
          console.log(err)
          callback(new Error('网络错误'))
        })
      }
      
      return {
        size: 'medium',
        uploadUrl: 'aaa',
        uploadForm: new FormData(),
        ruleForm: {
          templateId: '',
          templateAmount: '',
          templateAreas: '',
          templateFactory: '',
          templateStore: '',
          templateMemo: ''
        },
        rules: {
          templateId: [
            { validator: idValidate, trigger: 'blur' }
          ],
          templateAmount: [
            { validator: numberValidate, trigger: 'blur' }
          ],
          templateAreas: [
            { required: true, trigger: 'blur' }
          ],
          templateFactory: [
            { required: true, trigger: 'blur' }
          ],
          templateStore: [
            { required: true, trigger: 'blur' }
          ],
          templateMemo: [
            { required: true, trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      handleRemove(file, fileList) {
        // console.log(file, fileList)
      },
      handlePreview(file) {
        // console.log(file)
      },
      uploadOverrun() {
        this.$message({
          type: 'error',
          message: '上传文件个数超出限制!最多上传5张图片!'
        })
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let factory = (this.factory !== '') ? this.factory : this.ruleForm.templateFactory
            this.uploadForm.append('templateId', this.ruleForm.templateId)
            this.uploadForm.append('templateAmount', this.ruleForm.templateAmount)
            this.uploadForm.append('templateAreas', this.ruleForm.templateAreas)
            this.uploadForm.append('templateFactory', factory)
            this.uploadForm.append('templateStore', this.ruleForm.templateStore)
            this.uploadForm.append('templateMemo', this.ruleForm.templateMemo)
            console.log(this.uploadForm.getAll('images[]'))
            api.Stock.r(this.uploadForm).then(res => {
              this.$message.success("成功添加库存！")
              
              // this.uploadForm = new FormData()
              this.$refs[formName].resetFields()
              this.$router.push('/store')
            })
            this.$refs.uploadPic.submit()
            
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      newFiles(file) {
        this.uploadForm.append('images[]', file)
        // console.log(file)
        console.log(this.uploadForm.getAll('images[]'))
        return false
      }
    },
    computed: {
      ...mapGetters(['auth', 'factory'])
    },
    created() {
      this.size = (window.innerWidth < 767) ? 'mini' : 'medium'
      /*api.IsIdExist('ff').then(res => {
        console.log(res)
      })*/
    }
  }
</script>

<style type="text/stylus" lang="stylus" rel="stylesheet/stylus" scoped>
  @import "~assets/stylus/variable.styl"
  @import "Stock.styl"
</style>



